* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

#map {
  height: 100vh;
}

.group-icon {
  z-index: 2;
  background-color: #279c25b3;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  padding: 5px;
  font-size: 24px;
  display: flex;
}

.group-icon:after {
  content: "";
  z-index: 1;
  background-color: #12e80e4d;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  position: absolute;
  top: -10px;
  left: -10px;
}

.group-icon p {
  z-index: 3;
  font-size: 1.2rem;
}
